* {
	margin: 0;
	padding: 0;
	outline: 0px;
}

html,
body {
	-webkit-text-size-adjust: none;
}

body {
	min-width: 240px;
}

article,
section {
	display: block;
}

a {
	outline: none;
}

a img {
	outline: none;
}

img {
	border: 0;
}

ul,
ol {
	margin-left: 0px;
}

/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}

body {
	line-height: 1;
}

ol,
ul {
	list-style: none;
}

blockquote,
q {
	quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
	content: '';
	content: none;
}

b,
strong {
	font-weight: 700;
}

i,
em {
	font-style: italic;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

picture {
	height: fit-content;
	width: fit-content;
	display: block;
}

a {
	&:has(picture) {
		line-height: 1;
		max-height: fit-content;
		max-width: fit-content;
	}
}